import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function AuthStyle(){
    darkLightMode('main');
    darkLightMode('input');
    darkLightMode('button');
    setStyle();
    document.getElementsByTagName('body')[0].onresize = function(){setStyle()};
}

export default AuthStyle;

function setStyle(){
    eId("app").className = eId("app").className.replace("container-fluid", "container").replace(" card shadow-lg", "") + " card shadow-lg";
    eId("app").className = eId("app").className.replace(" pt-5", "") + " pt-5";
    
    if(eId('app').offsetWidth > eId('app').offsetHeight){
        eId("main").className = eId("main").className.replace(" mb-5", "") + " mb-5";
    } else {
        eId("main").className = eId("main").className.replace(" mb-2","") + " mb-2";
    }

    eId("main").className = eId("main").className.replace(" card-body","") + " card-body";

    setInput('input');
    setBtn('btn');
}

function setInput(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.03 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}
